import * as React from 'react';
import Loadable from 'react-loadable';

const Loading: React.ComponentType<any> = () => null;

export const CodeMirrorLazy = Loadable({
  loader: () => import('./Codemirror'),
  loading: Loading,
  render(loaded, props) {
    const ref = props.codeMirrorRef || undefined;
    return React.createElement(loaded.default, {
      ...props,
      ref: ref,
    })
  },
});
